.navbar {
  background-color: #333;
  height: 4em;
  position: fixed;
  display: flex;
  flex-direction: row;
  vertical-align: middle;
  width: 100%;
}

.navbar > div {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.title {
  color: #ffffff;
  font-size: 30px;
  font-weight: bold;
  margin-left: 1em;
}

.title-div > a {
  text-decoration: none;
}

@media only screen and (max-width: 750px) {
  .navbar {
    display: block;
    height: 5.5em;
  }
}
